import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import TableLayout from 'components/TableLayout';
import { setDevListColumns } from '../../cell/methods';

@inject('DeviceDefineStore')
@observer
class OrderTable extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeviceDefineStore;
	}
	render() {
		return <div>{this.renderTable()}</div>;
	}
	renderTable = () => {
		const { devList, devPage, isDevLoading } = toJS(this.props.DeviceDefineStore);
		const { onDelete } = this.props
		return (
			<TableLayout
				size="middle"
				style={{
					fontSize: '12px', marginTop: '20px'
				}}
				rowSelection={{
					selectedRowKeys: this.store.devListSelectedRowKeys,
					onChange: this.onSelectChange,
				}}
				rowKey='id'
				loading={isDevLoading}
				columns={setDevListColumns({
					onCodeQuery: this.onCodeQuery,
					onEdit: this.onEdit,
					onDelete: onDelete
				})}
				dataSource={devList}
				pagination={{
					...devPage,
					onChange: this.onPageChange
				}}
			/>
		);
	};
	onCodeQuery = (e) => {
		this.props.DeviceDefineStore.getSortList()
		this.props.DeviceDefineStore.getDevInfo({ id: e }).then(() => {
			const { sortList, devCurrent } = this.props.DeviceDefineStore
			this.props.DeviceDefineStore.sortList.map(per => {
				this.props.DeviceDefineStore.codeInfo[per.item] = devCurrent[per.item]
			})
			// console.log(this.props.DeviceDefineStore.sortList, this.props.DeviceDefineStore.codeInfo)
			this.props.DeviceDefineStore.codeVisible = true
		})
	}
	onEdit = (type, value) => {
		this.store.listStatus = type;
		this.store.listVisible = true;
		this.store.getDevInfo({
			id: value
		})
	}
	onSelectChange = selectedRowKeys => {
		this.store.devListSelectedRowKeys = selectedRowKeys
	}
	onPageChange = (page, pageSize) => {
		this.store.getDevList({
			pageSize,
			pageIndex: page,
		});
	};
	componentDidMount() {
		this.store.getDevList({ queryType: '' });
	}
}

export default OrderTable;
