import React, { Component } from 'react';
import { Modal } from 'antd';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import domtoimage from 'dom-to-image';
import image from 'assets/bg.png';
var QRCode = require('qrcode.react');

@inject('DeviceDefineStore')
@observer
class QrCanvas extends Component {
  constructor(props) {
    super(props)
    this.state = {
      codeInfo: toJS(this.props.DeviceDefineStore.codeInfo) || {}
    }
  }
  render() {
    const { codeInfo } = this.state
    const renderModuleCode = ()=>{
      let arr = [
        <div style={{ width: 180, height: 180, padding: 10, background: '#fff', marginBottom: 20 }}>
          <QRCode size={160} value={`http://file.boranet.cn/wx/getInfoByCode?deviceCode=${codeInfo.deviceCode}`} />
        </div>,
        <div style={{ flex: 1, marginLeft: 20, color: '#fff', fontSize: 24, marginTop: 0, letterSpacing: 2 }}>
          {this.codeInfo()}
        </div>
      ]
      return Object.keys(codeInfo).indexOf('right') > -1 ? arr.reverse() : arr;
    }
    return (
      <Modal
        title="铭牌"
        visible={this.props.DeviceDefineStore.codeVisible}
        width={700}
        okText="下载"
        cancelText="取消"
        onOk={this.clickOk}
        onCancel={e => this.props.DeviceDefineStore.codeChange()}
      >
      {/* || ${image} */}
        <div>
          <div id="my-node" style={{ background: `url(${image})`, backgroundSize: '100%', backgroundRepeat: 'no-repeat', padding: '20px 20px 0 40px', borderRadius: '12px' }}>
            {/*<div style={{ textAlign: 'right', color: '#fff', fontSize: 25, letterSpacing: 4 }}>{codeInfo.deviceName}</div>*/}
            <div style={{ display: 'flex' }}>
              {codeInfo && renderModuleCode()}
            </div>
          </div>
        </div>

      </Modal>
    );
  }
  codeInfo = () => {
    console.log(this.props.DeviceDefineStore.sortList,
      this.props.DeviceDefineStore.codeInfo, Object.keys(this.state.codeInfo))

    let arr = []
    for (let key in this.state.codeInfo) {
      console.log(key)
      switch (key) {
        case 'deviceName':
          arr.push(<p style={{ marginBottom: 10 }}>设备名称：{this.state.codeInfo[key]}</p>)
          break;
        case 'deviceCode':
          arr.push(<p style={{ marginBottom: 10 }}>设备编号：{this.state.codeInfo[key]}</p>)
          break;
        case 'modelId':
          arr.push(<p style={{ marginBottom: 10 }}>型号：{this.state.codeInfo[key]}</p>)
          break;
        case 'guaranteePeriod':
          arr.push(<p style={{ marginBottom: 10 }}>保修期：{this.state.codeInfo[key]}</p>)
          break;
        case 'unit':
          arr.push(<p style={{ marginBottom: 10 }}>计量单位：{this.state.codeInfo[key]}</p>)
          break;
        case 'deviceTime':
          console.log(this.state.codeInfo)
          arr.push(<p style={{ marginBottom: 10 }}>创建时间：{this.state.codeInfo['deviceTime'].split(' ')[0]}</p>)
          break;
        default:break;
      }

    }
    console.log(arr, 'arr')
    return arr
  }
  cancelCode = () => {
    this.props.DeviceDefineStore.codeChange()
  }
  clickOk = () => {
    var node = document.getElementById('my-node');
    // var node = `<div id="my-node" style={{ background: 'url(https://assets-boranet.oss-cn-hangzhou.aliyuncs.com/img/bg.png)', backgroundSize: '100%', backgroundRepeat: 'no-repeat', padding: '20px 20px 20px 20px'}}>
    // <div style={{ textAlign: 'right', color: '#fff', fontSize: 25, letterSpacing: 4 }}>{codeInfo.deviceName}</div>
    // <div style={{ display: 'flex' }}>
    //   <div style={{ width: 180, height: 180, padding: 10, background: '#fff' }}>
    //     <QRCode size={160} value="http://facebook.github.io/react/" />,
    // </div>
    //   <div style={{ flex: 1, marginLeft: 20, color: '#fff', fontSize: 24, marginTop: 20, letterSpacing: 2 }}>
    //     <p style={{ marginTop: 5 }}>设备编码：{codeInfo.deviceCode}</p>
    //     <p style={{ marginTop: 5 }}>型号：{codeInfo.modelId}</p>
    //     <p style={{ marginTop: 5 }}>单位：{codeInfo.unit}</p>
    //   </div>
    // </div>`
    domtoimage.toPng(node)
      .then((dataUrl) => {
        const a = document.createElement("a");
        const event = new MouseEvent("click");
        a.download = "图片"
        a.href = dataUrl;
        a.dispatchEvent(event);
        this.props.DeviceDefineStore.codeChange()
      })
      .catch(function (error) {
        console.error('oops, something went wrong!', error);
      });
  }
  componentDidMount() {
  }
}
export default QrCanvas