import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import GlobalList from 'components/GlobalList';
import { Modal } from 'antd';
import FileModal from './FileModal'
import { setDeviceTypeColumns, setDeviceTypeForms, } from '../../cell/methods';

@inject('DeviceDefineStore')
@observer
class DefineTable extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeviceDefineStore;
		this.state = {
			visible: false,
			selectedRowKeys: [],
			fileVisible: false
		}
	}
	render() {
		const {
			isLoading,
			status, visible, current, selectType,
			currentMain, typeList, typePage,
		} = this.store;
		const rowSelection = {
			selectedRowKeys: this.store.selectedRowKeys,
			onChange: this.props.handleSelect
		};
		return (
			<div>
				<GlobalList
					table={{
						rowKey: 'id',
						columns: setDeviceTypeColumns({
							current, handleEdit: this.handleEdit,
							selectList: selectType,
							handleFile: this.handleFileQuery,
							handleStatus: this.handleStatus
						}),
						dataSource: typeList,
						pagination: {
							...typePage,
							onChange: (pageIndex, pageSize) => {
								this.store.getType({
									pageIndex,
									pageSize,
								});
							}
						},
						loading: isLoading,
						rowSelection,
					}}
					modal={{
						visible,
						title: status === 'add' ? '新增' : '编辑',
						onCancel: this.handleVisible,
						onOk: this.handleSubmit,
						isForm: true,
						formColumns: setDeviceTypeForms({
							status, currentMain, current, selectList: selectType
						}),
					}}
				/>
				{
					this.state.fileVisible && <FileModal
						visible={this.state.fileVisible}
						onClose={this.handleFileQuery}
					/>
				}
			</div>
		);
	}
	handleFileQuery = (record) => {
		this.store.currentMain = record;
		this.setState({
			fileVisible: !this.state.fileVisible
		})
	}
	handleFileExport = (record) => {
		console.log(record)

	}
	handleFileDel = (type, id) => {
		if (type === 'batch') {
			if (this.state.selectedRowKeys.length < 1) {
				Modal.warn({
					title: '提示',
					content: '请选择需要删除的文件'
				});
			} else {
				Modal.confirm({
					title: '提示',
					content:
						'确定删除选中的' +
						this.state.selectedRowKeys.length +
						'条文件内容？',
					okText: '确定',
					cancelText: '取消',
					onOk: () => {
						this.store.delType(
							{
								ids: this.state.selectedRowKeys
							},
							'batch'
						);
					}
				});
			}
		} else {
			Modal.confirm({
				title: '提示',
				content: '确定删除当前文件内容？',
				okText: '确定',
				cancelText: '取消',
				onOk: () => {
					this.store.delType(
						{
							id
						},
						'single'
					);
				}
			});
		}
	}
	handleStatus = record => {
		let params = {}
		const { id, typeStatus, modelStatus } = record
		if (this.store.current === '1') {
			params = {
				id, typeStatus: typeStatus === 1 ? 0 : 1
			}
		} else {
			params = {
				id, modelStatus: modelStatus === 1 ? 0 : 1
			}
		}
		this.store.saveType({
			...params
		})
	}
	handleClose = () => {
		this.setState({
			visible: !this.state.visible
		})
	}
	handleFileSelect = selectedRowKeys => {
		this.setState({ selectedRowKeys })
	}
	/* modal - operate */
	handleVisible = () => {
		this.store.visible = !this.store.visible;
	};
	/* 弹窗提交操作 */
	handleSubmit = values => {
		const { currentMain } = this.store;
		let params = {
			...currentMain,
			...values
		}
		this.store.saveType(params);
		this.handleVisible();
	};
	/* 编辑弹窗 */
	handleEdit = record => {
		this.store.status = 'edit';
		this.store.currentMain = record;
		this.handleVisible();
	};
	componentDidMount() {
		this.store.getSelectType({})
	}
}
export default DefineTable;
