import React from 'react'
import { inject, observer } from 'mobx-react'
import { toJS } from 'mobx'
import DefineTable from './DefineTable'
import SearchBar from '../cell/SearchBar'
// import { Modal, Table } from 'antd'
import '../index.less'
@inject('DeviceDefineStore')
@observer
class DefinePage extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeviceDefineStore;
		this.state = {
			selectedRowKeys: [],
			current: ''
		};
	}
	render() {
		const rowSelection = {
			selectedRowKeys: toJS(this.store.selectedRowKeys),
			onChange: this.handleSelect
		}
		return <div className='define-list'>
			<SearchBar
			// handleDelete={this.handleDelete.bind(this,{type:'batch'})}
			/>
			<DefineTable
				// handleDelete = {this.handleDelete}
				rowSelection={rowSelection}
				handleSelect={this.handleSelect}
				selectedRowKeys={toJS(this.store.selectedRowKeys)}
			/>
		</div>;
	}
	handleSelect = selectedRowKeys => {
		this.store.selectedRowKeys = selectedRowKeys;
	};
	handleSelect = selectedRowKeys => {
		this.store.selectedRowKeys = selectedRowKeys;
	};

}

export default DefinePage