import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import {
	Modal,
	Table,
	Button,
	Select,
	Form,
	Input,
	Upload,
	Icon,
	message,
	Divider
} from 'antd';
import { setDeviceFileColumns, setFileType } from '../../cell/methods';
const { Option } = Select;
const formItemLayout = {
	labelCol: { span: 4 },
	wrapperCol: { span: 12 }
};
@inject('DeviceDefineStore')
@observer
class FileModal extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeviceDefineStore;
		this.state = {
			visible: false,
			selectedRowKeys: [],
			modelId: 0,
			fileName: ''
		};
	}
	render() {
		const { fileList, isLoading, selectModel, selectType, currentMain } = this.store;
		const rowSelection = {
			selectedRowKeys: this.props.selectedRowKeys,
			onChange: this.props.handleSelect
		};
		const { onClose } = this.props;
		const uploadConfig = (id) => {
			return {
				name: 'file',
				showUploadList: false,
				action: '/manage/base/file/fileUpload',
				headers: {
					token: sessionStorage.getItem('selfToken'),
				},
				multiple: true,
				data: {
					businessId: currentMain.id,
					businessType: 1,
					fileType: 1,
					type: id,
				},
				beforeUpload: this.beforeUpload,
				onChange(info) {
					if (info.file.status !== 'uploading') {
						console.log(info.file, info.fileList);
					}
					if (info.file.status === 'done') {
						message.success(`${info.file.name} 上传成功`);
					} else if (info.file.status === 'error') {
						message.error(`${info.file.name} 上传失败`);
					}
				}
			}
		};
		return (
			<Modal
				title={'查看知识库详情'}
				visible={this.props.visible}
				footer={
					<Button type='primary' onClick={onClose}>
						关闭
					</Button>
				}
				onCancel={this.props.onClose}
			>
				<div style={{ marginBottom: '10px' }}>
					<Button type='primary' onClick={this.handleFileAdd}>
						新增
					</Button>
					{/* <Button onClick={this.handleFileAdd}>删除</Button> */}
				</div>
				<Table
					size='small'
					rowSelection={rowSelection}
					dataSource={fileList}
					columns={setDeviceFileColumns({
						handleFileExport: this.handleFileExport,
						handleFileDel: this.handleFileDel
					})}
					isLoading={isLoading}
				/>
				{this.state.visible && (
					<Modal
						title='新增文档'
						visible={this.state.visible}
						onCancel={this.handleClose}
						onOk={this.handleClose}
						okText='确定'
						cancelText='取消'
					>
						<div>
							<Form.Item {...formItemLayout} label='设备类型'>
								<Select
									placeholder='选择设备类型'
									style={{ width: 250 }}
									showSearch
									onChange={this.onTypeChange}
									optionFilterProp='children'
								>
									{selectType.length > 0 && selectType.map(item => (
										<Option value={item.id} key={item.id}>
											{item.id}_{item.typeName}
										</Option>
									))}
								</Select>
							</Form.Item>
							<Form.Item {...formItemLayout} label='设备型号'>
								<Select
									placeholder='选择设备型号'
									style={{ width: 250 }}
									showSearch
									onChange={this.onModelChange}
									optionFilterProp='children'
								>
									{selectModel.length > 0 &&
										selectModel.map(item => (
											<Option value={item.id} key={item.id}>
												{item.modelName}
											</Option>
										))}
								</Select>
							</Form.Item>
						</div>
						<Divider />
						<ul style={{ marginTop: '10px', paddingLeft: '0' }}>
							{setFileType().map(item => {
								return (
									<li key={item.id}>
										<Form.Item {...formItemLayout} label='文档类型'>
											<Input
												placeholder='填写'
												style={{ width: '200px' }}
												value={item.typeName}
												disabled
											/>
										</Form.Item>
										<Form.Item {...formItemLayout} label='设备文档'>
											<Upload {...uploadConfig(item.id)}>
												<Button>
													<Icon type='upload' /> 选择文件
												</Button>

											</Upload>
											<span>文件名称</span>
										</Form.Item>
									</li>
								);
							})}
						</ul>
					</Modal>
				)}
			</Modal>
		);
	}
	onTypeChange = value => {
		this.store.getSelectModel({ typeId: value });
	};
	onModelChange = value => {
		this.setState({
			modelId: value
		});
	};
	handleFileAdd = () => {
		this.setState({
			visible: true
		});
	};
	beforeUpload = (file, fileList) => {
		this.setState({ fileName: file.name })
	}
	handleFileExport = record => {
		window.open(`/manage/base/file/download?fileType=${record.fileType}&type=${record.innertype}&businessId=${record.businessId}&businessType=1`)
		// this.store.downloadFile({
		// 	modelId:record.modelId,
		// 	type:record.type
		// })
	};
	handleFileDel = ({ type, value }) => {
		if (type === 'batch') {
			if (this.state.selectedRowKeys.length < 1) {
				Modal.warn({
					title: '提示',
					content: '请选择需要删除的文件'
				});
			} else {
				Modal.confirm({
					title: '提示',
					content:
						'确定删除选中的' +
						this.state.selectedRowKeys.length +
						'条文件内容？',
					okText: '确定',
					cancelText: '取消',
					onOk: () => {
						this.store.delFile(
							{
								ids: this.state.selectedRowKeys
							},
							'batch'
						);
					}
				});
			}
		} else {
			Modal.confirm({
				title: '提示',
				content: '确定删除当前文件内容？',
				okText: '确定',
				cancelText: '取消',
				onOk: () => {
					this.store.delFile(
						{
							id: value
						},
						'single'
					).then(() => {
						const { currentMain } = this.store; //当前model
						this.store.getDeviceFileList({
							businessType: 1,
							id: currentMain.id
						});
					});
				}
			});
		}
	};
	handleClose = () => {
		this.setState({
			visible: !this.state.visible
		});
		const { currentMain } = this.store; //当前model
		this.store.getDeviceFileList({
			businessType: 1,
			id: currentMain.id
		});
	};
	handleFileSelect = selectedRowKeys => {
		this.setState({ selectedRowKeys });
	};
	/* modal - operate */
	handleVisible = () => {
		this.store.visible = !this.store.visible;
	};
	/* 弹窗提交操作 */
	handleSubmit = values => {
		const { currentMain } = this.store;
		let params = {
			...values,
			...currentMain
		};
		this.store.saveType(params);
		this.handleVisible();
	};
	/* 编辑弹窗 */
	handleEdit = record => {
		this.store.status = 'edit';
		this.store.currentMain = record;
		this.handleVisible();
	};
	componentDidMount() {
		const { currentMain } = this.store; //当前model
		this.store.getDeviceFileList({
			businessType: 1,
			id: currentMain.id
		});
		this.store.getSelectType({})
	}
}
export default FileModal;
